.Card {
  height: 15rem;
  width: 21rem;
  display: flex;
  border: 1px solid grey;
}

.Card__Listings {
  padding-left: 1rem;
  flex: 3 1;
  font-family: "Nova Mono", serif;
  color: white;
}

.Card__Listings span {
  padding-left: 2rem;
}
.Card__Title {
  flex: 1 1;
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  -webkit-text-orientation: mixed;
          text-orientation: mixed;
  padding-top: 1rem;
  padding-right: 1rem;

  display: flex;
  flex-direction: column;
}

.card_countryName {
  flex: 1 1;
  font-weight: 600;
  color: lightgreen;
  font-size: 1.3rem;
}

.card_countryCurrency {
  letter-spacing: 1px;
  color: rgb(233, 233, 233);
  flex: 1 1;
  font-size: 0.75rem;
}

.Card__Listings-Day {
  padding-left: 1rem;
  flex: 3 1;
  font-family: "Nova Mono", serif;
  color: black;
}

